import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useParams, useHistory } from 'react-router-dom'
import {
  Row,
  Col,
  Image,
  ListGroup,
  Card,
  Button, Form
} from 'react-bootstrap'
import Rating from '../Rating'
import { listProductDetails } from '../../actions/productActions'
import Loader from '../Loader'
import Message from '../Message'

const ProductScreen = () => {
  //Router hooks
  const [qty, setQty] = useState(1)
  const { push } = useHistory()
  const { id } = useParams()
  //Use redux
  const dispatch = useDispatch()
  const productDetails = useSelector(state => state.productDetails)
  const { loading, error, product } = productDetails
  useEffect(() => {
    dispatch(listProductDetails(id))
  }, [id, dispatch]);
  //Get product and go to Cart
  const addToCartHandler = () => {
    push(`/cart/${id}?qty=${qty}`)
  }
  return (
    <>
      <Link className="btn btn-light my-3" to={`/`}>
        Go Back
      </Link>
      {loading ? <Loader></Loader> : error ? <Message variant="danger"></Message> : (<Row>
        <Col md="6">
          <Image src={product.image} alt={product.name} fluid />
        </Col>
        <Col md="3">
          <ListGroup variant="flush">
            <ListGroup.Item>
              <h2>{product.name}</h2>
            </ListGroup.Item>
            <ListGroup.Item>
              <Rating
                value={product.rating}
                text={`${product.numReviews} reviews`}
              ></Rating>
            </ListGroup.Item>
            <ListGroup.Item>Price:  {product.price}</ListGroup.Item>
            <ListGroup.Item>{product.description}</ListGroup.Item>
          </ListGroup>
        </Col>
        <Col md={3}>
          <Card>
            <ListGroup variant="flush">
              <ListGroup.Item>
                <Row>
                  <Col>Price:</Col>
                  <Col>
                    <strong> {product.price}</strong>
                  </Col>
                </Row>
              </ListGroup.Item>

              <ListGroup.Item>
                <Row>
                  <Col>Status:</Col>
                  <Col>
                    <strong>{product.countInStock > 0 ? `In Stock` : `Sold Out`}</strong>
                  </Col>
                </Row>
              </ListGroup.Item>
              {product.countInStock > 0 && (
                <ListGroup.Item>
                  <Row>
                    <Col>Qty</Col>
                    <Col>
                      <Form.Control
                        as='select'
                        value={qty}
                        onChange={(e) => setQty(e.target.value)}
                      >
                        {[...Array(product.countInStock).keys()].map(
                          (x) => (
                            <option key={x + 1} value={x + 1}>
                              {x + 1}
                            </option>
                          )
                        )}
                      </Form.Control>
                    </Col>
                  </Row>
                </ListGroup.Item>
              )}
              <ListGroup.Item>
                <Row>
                  <Col>
                    <Button
                      onClick={addToCartHandler}
                      className='btn-block '
                      disabled={true ? product.countInStock === 0 : false}>ADD TO CART</Button>
                  </Col>
                </Row>
              </ListGroup.Item>
            </ListGroup>
          </Card>
        </Col>
       
      </Row>)}

    </>
  )
}

export default ProductScreen
